<template>
	<div id="gohome-index">
		<public-header title="生活圈"></public-header>
		<div class="content">
			<div class="group">
				<van-row gutter="20">
					<van-col span="8" v-for="(item, index) in gohome" :key="item.title">
						<router-link tag="div" class="deliveryer-item" :to="util.getUrl({path: item.url})">
							<div class="top">
								<i :class="'icon' + ' ' + item.icon + ' ' + item.iconColor" slot="icon"></i>
							</div>
							<div class="bottom">
								<span>{{item.title}}</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				gohome: {},
				showPreLoading: true
			}
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'manage/gohome/index/index',
					autoAssign: true,
					variable: 'gohome',
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#gohome-index .content{
		background-color: #fff;
	}
	#gohome-index .content .group{
		padding: 20px 15px;
	}
	#gohome-index .content .group:last-child{
		padding-top: 0px;
	}
	#gohome-index .content .group .van-row{
		text-align: center;
	}
	#gohome-index .content .group .deliveryer-item{
		margin-bottom: 20px;
	}
	#gohome-index .content .group .deliveryer-item .top{
		padding: 10px;
	}
	#gohome-index .content .group .deliveryer-item .top .icon{
		font-size: 40px;
	}
	#gohome-index .content .group .deliveryer-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
</style>


